.process_warp {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.process_item_line {
  width: 3rem;
  height: 7rem;
  position: relative;
  top: 3rem;
  left: 2rem;
}
.process_item_warp {
  width: 19.03rem;
  height: 6.96rem;
  display: flex;
  justify-content: flex-start;
}
.process_item_right {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.process_item_icon {
  margin-left: 3rem;
}
.process_item_icon img {
  width: 2.8rem;
}
.text-warp {
  margin-left: 1rem;
  font-size: 1rem;
}
.mockRadio {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 2px solid #b0cafb;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mockRadio_in {
  width: 0.75rem;
  height: 0.75rem;
  background-color: #78a2f8;
  border-radius: 50%;
}
.mockLine {
  height: 5.7rem;
  width: 2px;
  opacity: 0.7;
  background-color: #b0cafb;
}

.rewriteMockLine {
  height: 4rem !important;
}
.mockAngal {
  width: 0;
  height: 0;
  border-width: 0 0.6rem 0.6rem;
  border-style: solid;
  border-color: transparent transparent #b0cafb;
  transform: rotateZ(180deg);
}
.process-wrap {
  padding-bottom: 8rem;
}
